<template>
  <el-form class="user" ref="userForm" :rules="rules" :model="user" label-width="90px">
    <el-form-item label="用户头像 :" prop="avatar_id">
      <div>
        <div v-if="url" class="user__image" @click="chooseFile">
          <img :src="url">
        </div>
          <el-button v-else type="success" @click="chooseFile" size="mini">头像上传</el-button>
          <input hidden type="file" ref="fileInput" @change="fileChangeHandle($event)" />
        </div>
    </el-form-item>
    <el-form-item label="用户名 :" prop="name">
      <el-input size="mini" class="user__input" v-model="user.name"></el-input>
    </el-form-item>
    <el-form-item label="电话 :" prop="phone">
      <el-input size="mini" class="user__input" v-model="user.phone"></el-input>
    </el-form-item>
    <el-form-item label="邮箱 :" prop="email">
      <el-input size="mini" class="user__input" v-model="user.email"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button size="mini" type="primary" @click="onSubmit">立即修改</el-button>
      <el-button size="mini">取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { userUpdate, getUserInfo } from "@/api/common.js";
import upload from "@/api/upload.js";

export default {
  name: "User",
  data() {
    const isEmail = (rule, value, callback) => {
      const pattern =  /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if(value && !pattern.test(value)) {
        return callback(new Error('请输入正确邮箱'));
      }else {
        callback();
      }
    }
    const isPhone = (rule, value, callback) => {
      if(!/^1[0-9]{10}$/.test(value)) {
        return callback(new Error('请输入正确手机号'));
      }else {
        callback();
      }
    }
    return {
      user: {
        name: "",
        email: "",
        phone: "",
        avatar_id: ""
      },
      rules: {
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
        ],
        email: [
          { validator: isEmail, trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '请输入电话', trigger: 'blur' },
          { validator: isPhone, trigger: 'blur' }
        ]
      },
      url: ""
    }
  },
  computed: {
    userAvatar: {
      get() {
        return this.$store.state.user.avatar;
      },
      set(val) {
        this.$store.commit("user/updateAvatar", val);
      }
    },
    userName: {
      get() {
        return this.$store.state.user.name;
      },
      set(val) {
        this.$store.commit("user/updateName", val);
      }
    }
  },
  methods: {
    getUserInfo() {
      getUserInfo().then(({ data: { name, phone, email, avatar_id, avatar } }) => {
        this.user.name = name;
        this.user.email = email;
        this.user.phone = phone;
        this.user.avatar_id = avatar_id;
        this.url = avatar ? avatar.file_url : "";
      })
    },
    onSubmit() {
      this.$refs['userForm'].validate((valid) => {
        if (valid) {
          userUpdate(this.user).then(() => {
            this.userAvatar = this.url;
            this.userName = this.user.name;
            this.$message.success("修改成功!");
          }).catch(data => {
            this.$message.error(data.response.data.message);
          })
        }
      });
    },
    chooseFile() {
      this.$refs.fileInput.click();
    },
    fileChangeHandle(evt) {
      const files = evt.target.files || evt.dataTransfer.files;
      Promise.all([...files].map(file => upload(file))).then((data) => {
        const { file_id, file_url } = data[0].data;
        this.user.avatar_id = file_id;
        this.url = file_url;
      });
    }
  },
  mounted() {
    this.getUserInfo();
  }
}
</script>

<style lang="scss">
$prefix: user;

.#{$prefix} {
  &__input {
    width: 300px;
  }

  &__image {
    width: 80px;
    height: 80px;
    border-radius: 100%;
    cursor: pointer;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
</style>